import { randomChats } from "../consts/randomChats";
import { APP_STATE } from "../consts/states";
import { CHAT_TYPE, ChatMessage } from "../models";
import AppManager from "./appManager";

class ChatManager {
  appManager: AppManager;


  constructor(appManager: AppManager) {
    this.appManager = appManager;
    this.init();
  }


  getRandomChatInterval() {
    return 5000;
  }

  getFlavorChat() {
     let chatText;
     let newMessage: ChatMessage; 


     chatText = randomChats[Math.floor(Math.random() * randomChats.length)];
     newMessage= {
       message: chatText,
       speaker: {
         type: CHAT_TYPE.HELPER,
         id: 0
       }
     }


     switch(this.appManager.state) {
       
        case APP_STATE.CAMPAIGN_SETUP:
        break;
        case APP_STATE.PLAYER_RECRUITMENT:
            const playerIndex = Math.floor(Math.random() * this.appManager.getPlayerManager().getRecruitablePlayers().length);
            chatText = "Pick meeee!";
            newMessage= {
              message: chatText,
              speaker: {
                type: CHAT_TYPE.PLAYER,
                id: this.appManager.getPlayerManager().getRecruitablePlayers()[playerIndex].id
              }
            }
        break;
        case APP_STATE.CHARACTER_CREATION:
            const charIndex = Math.floor(Math.random() * this.appManager.getCharacterManager().getCharacters().length);
            chatText = "Pick meeee!";
            newMessage= {
              message: chatText,
              speaker: {
                type: CHAT_TYPE.CHARACTER,
                id: this.appManager.getCharacterManager().getCharacterByIndex(charIndex).id
              }
            }
        break;
        case APP_STATE.MAIN_MENU:
        default:
            chatText = randomChats[Math.floor(Math.random() * randomChats.length)];
            newMessage= {
              message: chatText,
              speaker: {
                type: CHAT_TYPE.HELPER,
                id: 0
              }
            }
        break;
     }
     
     return newMessage;
     
     
     
     
    
  }

  init() {
    
  }


}

export default ChatManager;