import React from 'react';
import './notebook.scss'; 
import { ATTRIBUTE_CONTRIBUTOR, CharacterNotebookEntry } from '../../models';
import { useCharacterManager, usePlayerManager } from '../managerContexts';
import CImage from './cimage';
import AttributeDisplay from './attributeDisplay';


interface NoteBookCharacterProps {
    characterInfo: CharacterNotebookEntry;
}

const NotebookCharacter: React.FC<NoteBookCharacterProps> = ({ characterInfo }) => {
    const characterManager = useCharacterManager();
    const playerManager = usePlayerManager();

    const getPlayer = () => {
        
        const player = playerManager.getPlayerAssignedCharacter(characterInfo.character.id);
        if(player) {
            return `Assigned to ${player.name}`
        }
        return 'Not assiged to any player'
    }
    
    return (
        <div className="notebook-character">
            <div className="notebook-character-left ">
                <div className="notebook-left-top-container">
                    <div className="notebook-character-icon-container">
                        <CImage imageId={characterInfo.character.icon}/>
                    </div>
                    <div className="notebook-character-stats-container  notebook-text">  
                        <div className="notebook-character-stat"><b>Race: </b>{characterManager.getUFRace(characterInfo.character.race)}</div>
                        <div className="notebook-character-stat"><b>Class: </b>{characterManager.getUFClass(characterInfo.character.character_class)}</div>
                        <div className="notebook-character-stat"><b>Level: </b>{characterInfo.character.level}</div>
                        <div className="notebook-character-stat">{getPlayer()}</div>
                    </div>
                </div> 
                <div className = "notebook-character-combined-abilities  notebook-text">
                    <div className="notebook-character-abilities-title  header-text">
                        ABILITIES
                    </div>
                    <div className="notebook-character-abilities">
                        <AttributeDisplay character={characterInfo.character} category={ATTRIBUTE_CONTRIBUTOR.ALL} />
                    </div>
                </div>
            </div>
            <div className='notebook-character-right notebook-text'>
                <div className = "notebook-right-container">
                    <div className='notebook-character-ability-breakdown-container'>
                        <div className='notebook-character-ablilities-breakdown-title'>
                        </div>
                       
                        <div className = 'notebook-character-race-abilities'>
                            <div className='notebook-character-ability-breakdown header-text'>
                                {characterManager.getUFRace(characterInfo.character.race)} Abilities:  
                            </div>
                            <AttributeDisplay character={characterInfo.character} category={ATTRIBUTE_CONTRIBUTOR.RACE} collapsed={true} />
                        </div>
                       
                        <div className='notebook-character-ability-breakdown  header-text'>
                            {characterManager.getUFClass(characterInfo.character.character_class)} Abilities:  
                        </div>
                        <div className = 'notebook-character-ability-levels'>
                            <AttributeDisplay character={characterInfo.character} category={ATTRIBUTE_CONTRIBUTOR.CHARACTER_CLASS} collapsed={true}/>
                        </div>
                     {characterInfo.character.gear.length > 0 && (
                        <div>
                            <div className='notebook-character-ability-breakdown  header-text'>
                                Items: 
                            </div>
                            <AttributeDisplay character={characterInfo.character} category={ATTRIBUTE_CONTRIBUTOR.GEAR} collapsed={true} /> 
                        </div>   
                     )}                
                       
                    </div>
                </div>
            </div>
        </div>
    );
};

export default NotebookCharacter;