import { GEAR } from '../consts/characterConsts';
import { CHARACTER_CLASS, RACE } from '../consts/general';
import { ATTRIBUTE_TYPE, Character } from '../models';
import { generateName } from '../utils/utils';

class CharacterManager {
  private characters: Character[];
  private uniqueID: number = 0;
  private selectedCharacters: number[];

  constructor() {
    this.characters = [];
    this.selectedCharacters = [];
  }

  init() {
    this.generateAvailableCharacters(12);
  }

  getCharacters() {
    return this.characters;
  }

   getUFClass= (classId: CHARACTER_CLASS)  =>{
    switch(classId) {
      case CHARACTER_CLASS.CLERIC: return 'Cleric';
      case CHARACTER_CLASS.DRUID: return 'Druid';
      case CHARACTER_CLASS.FIGHTER: return 'Fighter';
      case CHARACTER_CLASS.PALADIN: return 'Paladin';
      case CHARACTER_CLASS.ROGUE: return 'Rogue';
      case CHARACTER_CLASS.WIZARD: return 'Wizard';
      default: return 'Unknown';
    }  
  }

   getUFRace= (raceId: RACE) => {
    switch(raceId) {
      case RACE.HUMAN: return 'Human';
      case RACE.ELF: return 'Elven';
      case RACE.DWARF: return 'Dwarven';
      case RACE.HALFLING: return 'Halfling';
      case RACE.HALF_ELF: return 'Half-Elf';
      case RACE.HALF_ORC: return 'Half-Orc';
      default: return 'Unknown';
    }
  }

  getUFAttributeName= (attributeId: ATTRIBUTE_TYPE) => {
    switch(attributeId) {
      case ATTRIBUTE_TYPE.COMBAT: return 'Combat';
      case ATTRIBUTE_TYPE.MAGIC: return 'Magic';
      case ATTRIBUTE_TYPE.TOOLS: return 'Tools';
      default: return 'Unknown';
    }
  }

  getUFAttributeSymbol= (attributeId: ATTRIBUTE_TYPE) => {
    switch(attributeId) {
      case ATTRIBUTE_TYPE.COMBAT: return '⚔️';
      case ATTRIBUTE_TYPE.MAGIC: return '✨';
      case ATTRIBUTE_TYPE.TOOLS: return '🔧';
      default: return '❓';
    }
  }

  getUFInitials= (character: Character) => {
    return character.name.split(' ').map((n) => n[0]).join('');
  }

  setSelectedCharacters(selectedCharacters:number[]) {
    this.selectedCharacters = this.characters
      .filter((_, index) => selectedCharacters.includes(index))
      .map(character => character.id);
  }

  getCharacterByIndex(index: number) {
    return this.characters[index];
  }

  getCharacterReferences() {
    return this.characters;
  }

  getSelectedCharacters() {
    return this.selectedCharacters;
  }

  getRandomRace() {
    const races = Object.values(RACE);
    const randomIndex = Math.floor(Math.random() * races.length);
    return races[randomIndex];
  }

  getRandomClass() {
    const classes = Object.values(CHARACTER_CLASS);
    const randomIndex = Math.floor(Math.random() * classes.length);
    return classes[randomIndex];
  }

  getRandomIconNumber() {
    return Math.floor(Math.random() * 2) + 1;
  }

  getRandomIcon(characterRace: RACE) {
    // currently each race has exactly two icons, named race_1.png and race_2.png. 
    // This function should return one of those two names
    switch(characterRace) {
      case RACE.DWARF:
        return 'dwarf_' + this.getRandomIconNumber();
      case RACE.ELF:
        return 'elf_' + this.getRandomIconNumber();
      case RACE.HUMAN:
        return 'human_' + this.getRandomIconNumber();
      case RACE.HALF_ORC:
        return 'halfOrc_' + this.getRandomIconNumber();
      case RACE.HALF_ELF:
        return 'halfElf_' + this.getRandomIconNumber();
      case RACE.HALFLING: 
        return 'halfling_' + this.getRandomIconNumber();
      default:
        return 'human_' + this.getRandomIconNumber();
    }
  }

  generateStartingGear() {
    switch(Math.floor(Math.random() * 6)) {
      case 0: return [GEAR.SWORD_1];
      case 1: return [GEAR.WAND_5];
      case 2: return [GEAR.HELPFUL_IMP];
     default: return [];
    }
  }

  




  generateAvailableCharacters(nbChars: number) {
    for (let p = 0; p < nbChars; p++) {
      const cclass = this.getRandomClass();
      const crace = this.getRandomRace();
      const newChar: Character = {
        name: generateName(true),
        race: crace,
        character_class: cclass,
        level: 1 + Math.floor(Math.random() * 4),
        icon: this.getRandomIcon(crace),
        abilities: [],
        id: this.uniqueID++,
        gear: this.generateStartingGear(),
      }
      this.characters.push(newChar)
    }
    return this.characters;
  }

  // Other methods to manage Characters
}

export default CharacterManager;