// Linear interpolation between two numbers
export const lerp = (a: number, b: number, t: number): number => {
  return a + (b - a) * t;
};

// Linear interpolation between two colors
export const lerpColor = (a: string, b: string, d: number, l: number): string => {

  const t = l / d;

  const ah = parseInt(a.replace(/#/g, ''), 16);
  const ar = ah >> 16;
  const ag = (ah >> 8) & 0xff;
  const ab = ah & 0xff;

  const bh = parseInt(b.replace(/#/g, ''), 16);
  const br = bh >> 16;
  const bg = (bh >> 8) & 0xff;
  const bb = bh & 0xff;

  const rr = Math.round(ar + t * (br - ar));
  const rg = Math.round(ag + t * (bg - ag));
  const rb = Math.round(ab + t * (bb - ab));

  return `#${((1 << 24) + (rr << 16) + (rg << 8) + rb).toString(16).slice(1)}`;
};

export const getStateColor = (state: APP_STATE): string => {
  let newModeColor = COLORS.STATE.MAIN_MENU
  switch(state) {
    case APP_STATE.MAIN_MENU:
      newModeColor = COLORS.STATE.MAIN_MENU;
    break;
    case APP_STATE.PLAYER_RECRUITMENT:
      newModeColor = COLORS.STATE.PLAYER_RECRUITMENT;
    break;
    case APP_STATE.CHARACTER_CREATION:
      newModeColor = COLORS.STATE.CHARACTER_CREATION;
    break;
    case APP_STATE.CAMPAIGN_SETUP:
      newModeColor = COLORS.STATE.CAMPAIGN_SETUP;
    break;
    case APP_STATE.SESSION_PREP:
      newModeColor = COLORS.STATE.SESSION_PREP;
    break;
    case APP_STATE.SESSION:
      newModeColor = COLORS.STATE.SESSION;
    break;
    case APP_STATE.SCORE:
      newModeColor = COLORS.STATE.SCORE;
    break;
  }
  return newModeColor;
}

export const randomElement = (someArray: any[]) : any => {
  return someArray[Math.floor(Math.random() * someArray.length)]
}

export const generateName = (character = true): string => {

  const firstName = randomElement(character ? NAMES.CHARACTER_NAMES[0] : NAMES.PLAYER_NAMES[0]);
  const lastName = randomElement(character ? NAMES.CHARACTER_NAMES[1] : NAMES.PLAYER_NAMES[1]);

  return firstName + ' ' + lastName;
}