import {Campaign, CampaignNode, Encounter, Graph } from '.././models';
import { CAMPAIGNS } from '../consts/campaigns';

class CampaignManager {
  private graph: Graph;

  constructor() {
    this.graph = new Graph();
  }

  getGraph() {
    return this.graph   
  }

  addPlace(place: string) {
    this.graph.addNode(place);
  }

  addEncounter(encounter: string) {
    this.graph.addNode(encounter);
  }

  connectNodes(from: string, to: string, weight: number) {
    this.graph.addEdge(from, to, weight);
  }

  getAvailableCampaigns() {
    return CAMPAIGNS;
  }

  getTagsForEncounter(encounter: Encounter) {
    // collect tags from linked location, and NPCs, and Combats....
    let tags = encounter.tags;
    tags = [...tags, ...encounter.location.tags];
    // tags = {...tags, ...encounter.npcs.tags};
    // tags = {...tags, ...encounter.combat.tags};
    // removes dupes
    return Array.from(new Set(tags));
  }


  static validateTags(encounters: Encounter[]) {
    // check that all tags are defined
    for(let i = 0; i < encounters.length; i++) {
      for(let j = 0; j < encounters[i].tags.length; j++) {
        if(encounters[i].tags[j] === undefined) {
          console.error(`Encounter ${encounters[i].name} tag ${j} is undefined`);
        }
      }
      for(let j = 0; j < encounters[i].location.tags.length; j++) {
        if(encounters[i].location.tags[j] === undefined) {
          console.error(`Encounter ${encounters[i].name} location ${encounters[i].location.name} tag ${j} is undefined`);
        }
      }
    }
  }

  //   static getBranchesForEncounter(encounter: Encounter, campaign: Campaign) {
  //   // get all encounters in the campaign
  //   const encounters = campaign.encounters;
  //   // find the encounter in the campaign
  //   const encounterIndex = encounters.findIndex((e) => e.name === encounter.name);
  //   // get the next encounter
  //   const nextEncounters = campaign.paths[encounterIndex];
  //   const nextEncounterNames = nextEncounters.map((index) => {
  //     return encounters[index].name;
  //   });
  //   return nextEncounterNames;
  // }

  // TODO: These arent always 1:1 - or are they? Make a campaign which is NOT 
  static createCampaignNodes(encounters: Record<string, Encounter>) : Record<string, CampaignNode> {
    const campaignNodes: Record<string, CampaignNode> = {};
    Object.keys(encounters).forEach((key) => {
      campaignNodes[key] = {
        encounter: encounters[key],
        pathNodes: []
      }
    })
    
    return campaignNodes;
  }

  // Other methods to manage the campaign
}

export default CampaignManager;