import DataManager from './dataManager';

class PlanningManager {

  private dataManager: DataManager | undefined;
  constructor(dataManager: DataManager) {
    this.init(dataManager);
  }

  init(dataManager: DataManager) {
    this.dataManager = dataManager;
  }
  
  getCampaignGraph() {
    return this.dataManager?.getGraph();
  }

  adjustWeights(from: string, to: string, weight: number) {
    const campaignGraph = this.getCampaignGraph();
    campaignGraph?.addEdge(from, to, weight);
  }
  // Other methods to manage planning
}

export default PlanningManager;