import React, { useState } from 'react';
import './playerCard.scss';
import { NOTEBOOK_CATEGORY, Player } from '../models';
import Cimage from './UI/cimage';
import Schedule from './schedule';
import PlayerPreferences from './playerPreferences';
import CharacterCard from './characterCard';
import characterManager from '../managers/characterManager';
import { useAppManager, useCharacterManager, usePlayerManager, useUiManager } from './managerContexts';
import MoreInfo from './UI/moreInfoButton';
import { APP_STATE } from '../consts/states';

interface PlayerCardProps {
  player: Player;
  isSelected: boolean;
  showSchedule?: boolean;
  showCharacters?: boolean;
  showPreferences?: boolean;
  onSelect: () => void;
  overlayStyle?: 'assign' | 'unassign' | 'none';
  parentUpdate?: () => void;
}

const PlayerCard: React.FC<PlayerCardProps> = ({ player, parentUpdate, isSelected, onSelect, showCharacters, showSchedule, showPreferences, overlayStyle }) => {
  const uiManager = useUiManager();
  const appManager = useAppManager();
  const [update, setUpdate] = useState(false);
  const [showTooltipLikes, setShowTooltipLikes] = useState(false);
  const [showTooltipHates, setShowTooltipHates] = useState(false);
  const [showTooltipTraits, setShowTooltipTraits] = useState(false);


  const mainLike = player.preferences.reduce((prev, current) => (current[1] > prev[1] ? current : prev), player.preferences[0]);
  const mainHate = player.preferences.reduce((prev, current) => (current[1] < prev[1] ? current : prev), player.preferences[0]);
  const otherLikes = player.preferences.filter(pref => pref[1] > 0 && pref !== mainLike);
  const otherHates = player.preferences.filter(pref => pref[1] < 0 && pref !== mainHate);

  const handleMouseEnterLikes = () => setShowTooltipLikes(true);
  const handleMouseLeaveLikes = () => setShowTooltipLikes(false);

  
  const handleMouseEnterHates = () => setShowTooltipHates(true);
  const handleMouseLeaveHates = () => setShowTooltipHates(false);

  const handleMouseEnterTraits = () => setShowTooltipTraits(true);
  const handleMouseLeaveTraits = () => setShowTooltipTraits(false);


  const allLikes = player.preferences.filter(pref => pref[1] > 0);
  const allHates = player.preferences.filter(pref => pref[1] < 0);
 

  const text = overlayStyle === 'assign' ? 'Assign' : 'Remove';
  const overlay = overlayStyle === 'none' ? '' : 
    <div className={`overlay ${overlayStyle}`}>
      <div className="overlay-text">
        {text}  
      </div>
    </div>;
  const characterManager = useCharacterManager();
  const playerManager = usePlayerManager();

  const assignedCharClicked = (characterIndex: number) => {
    if(appManager.state === APP_STATE.CHARACTER_CREATION) {
      unassignCharacter(characterIndex);
    }else {
      const char = characterManager.getCharacterByIndex(characterIndex);
      uiManager.showBasicNotebook(NOTEBOOK_CATEGORY.CHARACTER, char);
    }
  }

  const unassignCharacter = (characterIndex: number) => {
      playerManager.toggleSinglePlayerForCharacter(player, characterIndex);
      setUpdate(!update);
      if(parentUpdate){
        parentUpdate();
      }
    }
  
  const showNotebookForPlayer = () => {
    uiManager.showBasicNotebook(NOTEBOOK_CATEGORY.PLAYER, player)
  }
  return (
    <div className={`player-card ${isSelected ? 'selected' : ''}`} onClick={onSelect}>
      <MoreInfo callback={showNotebookForPlayer} />
      <div className="player-card-header">
        <div className="player-icon">
          <Cimage imageId={player.icon} />
        </div>
        <div className="player-upper-info">
          <div className="player-name">
            {player.name}
          </div>
          {showSchedule && (
            <div className="player-schedule-container">
              <Schedule dayData={player.schedule} />
            </div>
          )}
        </div>
      </div>
      {showPreferences && (
        <div className="player-preferences-section">
           {player.traits.length > 0 && (
            <div className="preferences-column">
                <div className="preferences-title">
                  Traits
                </div>
                <div className="preferences-traits " onMouseEnter={handleMouseEnterTraits} onMouseLeave={handleMouseLeaveTraits}>
                  {player.traits.map((trait, idx) => (
                    <div key={idx} className="trait-item text-pill trait">
                      {trait.name}
                    </div>
                  ))}
                </div>
            
              {showTooltipTraits && (
              <div className="tooltip traits">
                <div className="tooltip-content-single">
                  {player.traits.map((trait, idx) => (
                  <div key={idx} className="tooltip-trait-item">
                    <b>{trait.name}</b>: {trait.description}
                  </div>
                ))}
                </div>
              </div>
            )}
            </div>
           )}
          <div className="preferences-column">
            <div className="preferences-title">Likes</div>
            <div className="preferences-main">{mainLike[0]}</div>
            <div className="preferences-dots"  onMouseEnter={handleMouseEnterLikes} onMouseLeave={handleMouseLeaveLikes}>
              {allLikes.map((like, idx) => (
                <div key={idx} className="preference-dot like" title={like[0]} />
              ))}
            </div>
            {showTooltipLikes && (
              <div className="tooltip like">
                <div className="tooltip-content">
                  {player.preferences.filter(pref => pref[1] > 0).map((pref, idx) => (
                    <div key={idx} className="tooltip-pill">{pref[0]}</div>
                  ))}
                </div>
              </div>
            )}
          </div>
          <div className="preferences-column" >
            <div className="preferences-title">Hates</div>
            <div className="preferences-main">{mainHate[0]}</div>
            <div className="preferences-dots" onMouseEnter={handleMouseEnterHates} onMouseLeave={handleMouseLeaveHates}>
              {allHates.map((hate, idx) => (
                <div key={idx} className="preference-dot hate" title={hate[0]} />
              ))}
            </div>
            {showTooltipHates && (
              <div className="tooltip hate">
                <div className="tooltip-content">
                  {player.preferences.filter(pref => pref[1] < 0).map((pref, idx) => (
                    <div key={idx} className="tooltip-pill">{pref[0]}</div>
                  ))}
                </div>
              </div>
            )}
          </div>
        
        </div>
      )}
      {showCharacters && (
        <div className="character-row-container">
          <div className="character-row-display">
            {player.characters.map((characterIndex) => {
              const character = characterManager.getCharacterByIndex(characterIndex);
              return (
                <CharacterCard
                  key={character.id}
                  character={character}
                  isSelected={false}
                  onSelect={() => { assignedCharClicked(characterIndex) }}
                  showAbilities={false}
                  collapsed={true}
                />
              );
            })}
          </div>
          </div>
      )}
      {overlayStyle && overlay}
    </div>
  );
}

export default PlayerCard;