
import React from 'react';
import './globalUi.scss';


interface MoreInfoProps {
   callback: () => void;
}

const MoreInfo: React.FC<MoreInfoProps> = ({ callback }) => {
    return (
        <div className='more-info' onClick={callback}>
            <div className='more-info-text'>
                ?
            </div>
        </div>
    );
};

export default MoreInfo;