export function getRaceAttributes(character: Character) {
    switch(character.race) {
      case RACE.DWARF:
        return { [ATTRIBUTE_TYPE.COMBAT]: 2, 
                 [ATTRIBUTE_TYPE.MAGIC]: 0,
                 [ATTRIBUTE_TYPE.TOOLS]: 1
                };
      case RACE.ELF: 
        return { [ATTRIBUTE_TYPE.COMBAT]: 0, 
                 [ATTRIBUTE_TYPE.MAGIC]: 3,
                 [ATTRIBUTE_TYPE.TOOLS]: 0
              };
      case RACE.HUMAN: 
        return { [ATTRIBUTE_TYPE.COMBAT]: 1, 
                 [ATTRIBUTE_TYPE.MAGIC]: 1,
                 [ATTRIBUTE_TYPE.TOOLS]: 1
            };
      case RACE.HALFLING:
        return { [ATTRIBUTE_TYPE.COMBAT]: 0, 
                 [ATTRIBUTE_TYPE.MAGIC]: 0,
                 [ATTRIBUTE_TYPE.TOOLS]: 3
              };
      case RACE.HALF_ORC: 
        return { [ATTRIBUTE_TYPE.COMBAT]: 3, 
                 [ATTRIBUTE_TYPE.MAGIC]: 0,
                 [ATTRIBUTE_TYPE.TOOLS]: 0
              };
      case RACE.HALF_ELF:
        return { [ATTRIBUTE_TYPE.COMBAT]: 1, 
                 [ATTRIBUTE_TYPE.MAGIC]: 2,
                 [ATTRIBUTE_TYPE.TOOLS]: 0
              };
    }
  }

export function getGearAttributes(character: Character) {
  const gearAttributes = {
    [ATTRIBUTE_TYPE.COMBAT]: 0,
    [ATTRIBUTE_TYPE.MAGIC]: 0,
    [ATTRIBUTE_TYPE.TOOLS]: 0,
  };
  character.gear.forEach(gear => {  
    gearAttributes[ATTRIBUTE_TYPE.COMBAT] += gear.attributes[ATTRIBUTE_TYPE.COMBAT] ?? 0;
    gearAttributes[ATTRIBUTE_TYPE.MAGIC] += gear.attributes[ATTRIBUTE_TYPE.MAGIC] ?? 0;
    gearAttributes[ATTRIBUTE_TYPE.TOOLS] += gear.attributes[ATTRIBUTE_TYPE.TOOLS] ?? 0;
    });
    return gearAttributes;
}

export function getCombinedAttributes(character: Character) {

    const emptyAttribute = {
        [ATTRIBUTE_TYPE.COMBAT]: 0,
        [ATTRIBUTE_TYPE.MAGIC]: 0,
        [ATTRIBUTE_TYPE.TOOLS]: 0
    }
  const raceAttributes = getRaceAttributes(character);
  const gearAttributes = getGearAttributes(character);
  const classAttributes = getCombinedClassAttributes(character);

  const combinedAttributes: Attribute = {
    [ATTRIBUTE_TYPE.COMBAT]: 
      raceAttributes[ATTRIBUTE_TYPE.COMBAT] + 
      classAttributes[ATTRIBUTE_TYPE.COMBAT] + 
      gearAttributes[ATTRIBUTE_TYPE.COMBAT],
    [ATTRIBUTE_TYPE.MAGIC]: 
      raceAttributes[ATTRIBUTE_TYPE.MAGIC] + 
      classAttributes[ATTRIBUTE_TYPE.MAGIC] + 
      gearAttributes[ATTRIBUTE_TYPE.MAGIC],
    [ATTRIBUTE_TYPE.TOOLS]: 
      raceAttributes[ATTRIBUTE_TYPE.TOOLS] + 
      classAttributes[ATTRIBUTE_TYPE.TOOLS] + 
      gearAttributes[ATTRIBUTE_TYPE.TOOLS]
  };
  return combinedAttributes;
}


export function getCombinedClassAttributes(character: Character) {
    const baseAttribute = {
        [ATTRIBUTE_TYPE.COMBAT]: 0,
        [ATTRIBUTE_TYPE.MAGIC]: 0,
        [ATTRIBUTE_TYPE.TOOLS]: 0
    }
    for(let l=0; l< character.level; l++){
        let level = l+1;
        baseAttribute[ATTRIBUTE_TYPE.COMBAT] += getClassAttributes(character.character_class, level)[ATTRIBUTE_TYPE.COMBAT];
        baseAttribute[ATTRIBUTE_TYPE.MAGIC] += getClassAttributes(character.character_class, level)[ATTRIBUTE_TYPE.MAGIC];
        baseAttribute[ATTRIBUTE_TYPE.TOOLS] += getClassAttributes(character.character_class, level)[ATTRIBUTE_TYPE.TOOLS];
    }
    return baseAttribute;

}

  // This is NOT a total for all levels. It is how much a char gains for a SPECIFIC level. 
export function getClassAttributes(characterClass: CHARACTER_CLASS, level: number) {
  
    const oddLevel = level % 2;
    const evenLevel = (level +1) % 2;
    switch(characterClass) {
      case CHARACTER_CLASS.CLERIC: //4
        return { [ATTRIBUTE_TYPE.COMBAT]: 1 + oddLevel, 
                 [ATTRIBUTE_TYPE.MAGIC]: 2,
                 [ATTRIBUTE_TYPE.TOOLS]: evenLevel
            }
        case CHARACTER_CLASS.DRUID: //4.5
            return { [ATTRIBUTE_TYPE.COMBAT]: 1 + evenLevel, 
                     [ATTRIBUTE_TYPE.MAGIC]: 1 + oddLevel,
                     [ATTRIBUTE_TYPE.TOOLS]: 1 + oddLevel 
                };
        case CHARACTER_CLASS.FIGHTER: //4
            return { [ATTRIBUTE_TYPE.COMBAT]: 3, 
                     [ATTRIBUTE_TYPE.MAGIC]: 0,
                     [ATTRIBUTE_TYPE.TOOLS]: 1
                };
        case CHARACTER_CLASS.PALADIN: //4
            return { [ATTRIBUTE_TYPE.COMBAT]: 2 + evenLevel , 
                     [ATTRIBUTE_TYPE.MAGIC]: 1 ,
                     [ATTRIBUTE_TYPE.TOOLS]: oddLevel
                };
       
        case CHARACTER_CLASS.ROGUE: //4.5
            return { [ATTRIBUTE_TYPE.COMBAT]: 1, 
                     [ATTRIBUTE_TYPE.MAGIC]: 0,
                     [ATTRIBUTE_TYPE.TOOLS]: 3 + oddLevel
                };
    
        case CHARACTER_CLASS.WIZARD: //4.5
            return { [ATTRIBUTE_TYPE.COMBAT]: evenLevel, 
                     [ATTRIBUTE_TYPE.MAGIC]: 3,
                     [ATTRIBUTE_TYPE.TOOLS]: 1
                };
    }
    return { [ATTRIBUTE_TYPE.COMBAT]: 0,
                [ATTRIBUTE_TYPE.MAGIC]: 0,
                [ATTRIBUTE_TYPE.TOOLS]: 0
    }
}